/// <reference path="./lib/UserDataFormProps.d.ts" />
import React from "react";
import { InputAdornment, TextField, IconButton, Typography, Button, CircularProgress, FormControlLabel, Switch } from "@material-ui/core";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import Input from "../Input";
import RolInput from "./RolInput";
import UserListInput from "./UserListInput";


export default function (props: UserDataFormProps) {

    const {
        loading, id, onSubmit, onChange, formType, editionType,
        ...state } = props;


    function _onSubmit(event) {
        event.preventDefault();
        event.stopPropagation();
        onSubmit();
    }

    const editarEstado = formType === "EDIT" && editionType !== "USER" ? true : null;
    const editarModificarPVPs = (formType === "EDIT" && editionType !== "USER" && +state.rol === 3) ? true : null;
    const editarTermsAndConds = formType === "EDIT" && editionType !== "USER";

    const error = props.error || {};

    return (<form style={{ margin: "auto", maxWidth: 600 }} noValidate onSubmit={_onSubmit}>
        <Typography variant="h6">Información Personal</Typography>
        <Input
            disabled={loading}
            error={error.nombre}
            onChange={onChange}
            value={state.nombre || ""}
            name="nombre"
            label="Nombres Completos"
        />
        <Input
            disabled={loading}
            error={error.identificacion}
            onChange={onChange}
            value={state.identificacion || ""}
            name="identificacion"
            label="Identificacion (CI o Pasaporte)"
            type="phone"
        />
        <Input
            disabled={loading}
            error={error.telefono}
            onChange={onChange}
            value={state.telefono || ""}
            name="telefono"
            label="Telefono"
            type="phone"
        />
        <Input
            disabled={loading}
            error={error.email}
            onChange={onChange}
            value={state.email || ""}
            name="email"
            label="Correo Electronico"
            type="email"
        />
        <Input
            disabled={loading}
            error={error.nacionalidad}
            onChange={onChange}
            value={state.nacionalidad || ""}
            name="nacionalidad"
            label="Nacionalidad"
        />
        <Input
            InputLabelProps={{ shrink: true }}
            disabled={loading}
            error={error.fechaNacimiento}
            onChange={onChange}
            value={state.fechaNacimiento || ""}
            name="fechaNacimiento"
            label="Fecha Nacimiento"
            type="date"
        />

        <Typography variant="h6" style={{ marginTop: 30 }}>{formType === "SIGN-IN" ? "Agrega tu dirección" : "Dirección"}</Typography>
        <Input disabled={loading}
            error={error.ciudad}
            onChange={onChange}
            value={state.ciudad || ""}
            name="ciudad"
            label="Ciudad"
        />
        <Input disabled={loading}
            error={error.direccion}
            onChange={onChange}
            value={state.direccion || ""}
            name="direccion"
            label="Direccion"
        />

        <Typography variant="h6" style={{ marginTop: 30 }}>{formType === "SIGN-IN" ? "Crea tu Usuario" : "Usuario"}</Typography>
        <Input
            disabled={loading}
            error={error.username}
            onChange={onChange}
            value={state.username || ""}
            name="username"
            label="Usuario"
        />
        <Password
            required={formType === "SIGN-IN"}
            disabled={loading}
            error={error.password}
            onChange={onChange}
            value={state.password || ""}
            name="password"
            label={formType === "EDIT" ? "Nueva contraseña" : "Contraseña"}
        />
        <Password
            required={formType === "SIGN-IN"}
            disabled={loading}
            error={error.confirm_password}
            onChange={onChange}
            value={state.confirm_password || ""}
            name="confirm_password"
            label={formType === "EDIT" ? "confirma la nueva contraseña" : "Confirma tu contraseña"}
        />

        <UserListInput
            value={state.parentId}
            readOnlyValue={editionType === "ADMIN" ? undefined : state.ParentName}
            onChange={(value: any) => onChange({ currentTarget: { name: "parentId", value } } as any)}
            error={error.parentId}
            loading={loading}
        />

        {formType === "EDIT"
            ? <RolInput value={"" + (state.rol || "")} loading={loading} onChange={onChange} readOnly={editionType !== "ADMIN"} />
            : null}

        {editarEstado &&
            <Input
                select
                disabled={loading}
                value={(state.estado === undefined || state.estado === null) ? "" : state.estado.toString()}
                name="estado"
                label="Estado"
                SelectProps={{ native: true }}
                onChange={onChange}
            >
                <option value="1" >PENDIENTE</option>
                <option value="0" >APROBADO (Habilitado)</option>
                <option value="2" >RECHAZADO (Deshabilitado)</option>
            </Input>}

        {editarModificarPVPs &&
            <FormControlLabel
                label="Puede modificar precios durante toma de pedido"
                control={<Switch
                    checked={Boolean(state.editarPVPsdurantePedido)}
                    name="editarPVPsdurantePedido"
                    onChange={onChange}
                />}
            />
        }

        {editarTermsAndConds &&
            <div style={{ margin: "auto", textAlign: "center", paddingTop: 20, paddingBottom: 20 }}>
                <FormControlLabel
                    control={<input
                        type="checkbox"
                        onChange={onChange}
                        name="mustAcceptTermsAndConds"
                        checked={state.mustAcceptTermsAndConds || false}
                    />}
                    label="Mostrar Aceptar Terminos y Condiciones; y aceptar Políticas de privacidad al iniciar"
                />
                {state.termsAndCondsAcceptedAt && <div style={{ marginTop: 10 }}>
                    <Typography variant="button">
                        Este usuario ha aceptado los Términos y Condiciones el:
                        {new Date(state.termsAndCondsAcceptedAt).toLocaleString()}
                    </Typography>
                </div>}
            </div>}


        {(error.guardar) ? <Typography tabIndex={0} id="guardar" color="secondary">{error.guardar}</Typography> : null}
        {formType === "SIGN-IN" &&
            (<Button type="submit" color="primary" variant="outlined" fullWidth style={{ marginTop: 20 }} >
                {loading ? <CircularProgress size={20} color="inherit" /> : "ENVIAR"}
            </Button>)}
        {props.children}
    </form >
    );
}
function Password(props: any) {
    const { error, ...other_props } = props;
    const [show_password, switchShowPassword] = React.useState(false);
    var input_props: any = { inputProps: { id: props.name } };
    if (error) {
        input_props.error = true;
        input_props.helperText = error;
    }
    return (<TextField
        type={show_password ? "text" : "password"}
        required
        fullWidth
        {...other_props}
        {...input_props}
        style={{ marginTop: 5, marginBottom: 5 }}
        InputProps={{
            endAdornment: <InputAdornment position="end" >
                <IconButton onClick={x => switchShowPassword(!show_password)}>
                    {show_password ? <Visibility /> : <VisibilityOff />}
                </IconButton>
            </InputAdornment>
        }}
    />);
}