import React from "react";
import { Paper, Typography, TextField, Button, Divider, CircularProgress, Switch, FormControlLabel, Link } from "@material-ui/core";
import ImageInput from "../ImageInput";
export default function (props: {
    onValueChange: Function;
    numeroTransaccion: string;
    onImgChange: (imageFile?: File) => void;
    usarImagen?: boolean;
    imgTransaccion: string;
    items: any[];
    onDone: any;
    total_pagar: number;
    loading: boolean;
    img_trf_is_loading: boolean;
    visible: boolean;
}): JSX.Element {
    const PUEDE_CAMBIAR_A_NUMERO = null;
    return (<Paper style={{ maxWidth: 400, padding: 10, marginTop: 10, height: "100%", marginLeft: "auto", marginRight: "auto", display: props.visible ? "block" : "none" }}>
        <form onSubmit={props.onDone} style={{ textAlign: "center" }}>
            <Typography style={{ marginTop: 10, marginBottom: 10, textAlign: "center" }} variant="h6">Total a Pagar : {props.total_pagar.toCurrency()}</Typography>
            <Divider />
            <div style={{ padding: 20, paddingBottom: 0 }}>
                <Typography variant="h6" >Realiza tu pago mediante Transferencia o Deposito Bancario a la cuenta:</Typography>
                <Typography>BANCO PICHINCHA Cta. Ahorros #2205511631</Typography>
                <Typography>RUC. 1792627192001</Typography>
                <Typography>COSMETICOSNATU CIA LTDA</Typography>
                <div style={{ marginTop: 10 }}>
                    <Typography variant="h6">O realiza tu pago con tarjeta de crédito:</Typography>
                    {/*<!-- PayPal Logo -->*/}
                    <table cellPadding="10" cellSpacing="0" style={{ textAlign: "center", border: 0, margin: "auto", padding: 0 }} >
                        <tr>
                            <td align="center" style={{ padding: 0 }} >
                                <a href="https://www.paypal.me/natucosmeticoficial"
                                    target="_blank"
                                    title="Paga con paypal aqui"
                                >
                                    <img src="https://www.paypalobjects.com/webstatic/mktg/logo/AM_mc_vs_dc_ae.jpg" alt="PayPal Acceptance Mark"></img>
                                </a>
                            </td>
                        </tr>
                    </table>
                    {/*<!-- PayPal Logo -->*/}
                </div>
            </div>
            {PUEDE_CAMBIAR_A_NUMERO && <FormControlLabel
                label="Subir una Imagen"
                control={<Switch
                    disabled={props.loading}
                    checked={Boolean(props.usarImagen)}
                    onChange={e => props.onValueChange("usarImagen", e.currentTarget.checked)}
                />}
            />}

            {PUEDE_CAMBIAR_A_NUMERO && !props.usarImagen && <TextField
                style={{ width: "calc(100% - 20px)", margin: 10 }}
                variant="outlined"
                inputMode="numeric"
                inputProps={{ id: "numeroTransaccion" }}
                onChange={event => props.onValueChange("numeroTransaccion", event.currentTarget.value)}
                value={props.numeroTransaccion}
                label="Numero de Comprobante *"
            />}
            {props.usarImagen && <ImageInput
                loading={props.img_trf_is_loading}
                disabled={props.loading}
                className="transaction-img"
                onChange={props.onImgChange}
                src={props.imgTransaccion}
            />}

            <Button type="submit" disabled={props.loading} variant="outlined" color="primary" style={{ margin: 10, width: "calc(100% - 20px)" }}>
                {props.loading ? <CircularProgress size={25} /> : "ENVIAR"}
            </Button>
        </form>
    </Paper >)
}