import { Typography } from "@material-ui/core";
import Autocomplete from "@material-ui/lab/Autocomplete";
import React from "react";
import API from "../../Services/API";
import Input from "../Input";
export default function UserListInput(props: {
    onChange: (parentId: number) => void;
    value: number;
    readOnlyValue?: string;
    id?: string;
    error?: string;
    loading?: boolean;
}) {

    if (props.readOnlyValue) {
        return (<div style={{ display: "flex", flexDirection: "row" }} >
            <Typography>Madre: </Typography>
            <Typography variant="button" >{props.readOnlyValue}</Typography>
        </div>);
    }
    const [users, status] = GetUsersHook();


    if (props.value !== undefined && props.value !== null) {
        var value = users.find(x => x.id === props.value);
    }
    if (value === undefined) value = null;


    return <Autocomplete
        options={users}
        loading={status === "LOADING"}
        loadingText="Cargando datos..."
        noOptionsText="No se encontraron registros"
        renderInput={params => <Input
            label={status === "LOADING" ? "CARGANDO DATOS..." : "Selecciona el Asesor/a Natú que te invitó"}
            {...params}
            error={props.error}
            name="parentId"
        />}
        getOptionLabel={item => `${item.identificacion} - ${item.nombre}`}
        value={value}
        onChange={(_, value: VisibleUser) => props.onChange(value && value.id)}
        getOptionSelected={(item, value) => item.id === value.id}
    />
}



function GetUsersHook(): [VisibleUser[], "" | "LOADING" | "COMPLETED" | "ERROR", Error?] {
    const [status, setStatus] = React.useState<"" | "LOADING" | "COMPLETED" | "ERROR">("LOADING");
    const [users, setUsers] = React.useState([]);
    const [error, setError] = React.useState(null);

    React.useEffect(() => {
        if (status === "LOADING") {
            API.GetUsers(["id", "identificacion", "nombre"])
                .then(data => {
                    setUsers(data);
                    setStatus("COMPLETED");
                })
                .catch(ex => {
                    setStatus("ERROR");
                    setError(ex);
                });
        }
    }, [status]);
    return [users, status, error];
}