import React from "react";
import { InputProps, Typography } from "@material-ui/core";
import Input from "../Input";

export default function (props: {
    readOnly: boolean
    , loading: boolean
    , value: string
    onChange: InputProps["onChange"]
}) {
    if (props.readOnly)
        return (<div style={{ marginTop: 10, marginBottom: 10 }}>
            <Typography style={{ display: "inline" }} >ROL: </Typography>
            <Typography style={{ display: "inline" }} variant="button" >{RolToString(props.value)}</Typography>
        </div>);

    return (<Input
        select
        disabled={props.loading}
        value={props.value || ""}
        name="rol"
        label="Rol"
        SelectProps={{ native: true }}
        onChange={props.onChange}
    >
        <option value="1" >{RolToString(1)}</option>
        <option value="2" >{RolToString(2)}</option>
        <option value="3" >{RolToString(3)}</option>
    </Input>)
}


function RolToString(rol: number | string) {
    if (+rol === 1) return "ASESOR/A";
    if (+rol === 2) return "GUÍA";
    if (+rol === 3) return "ADMINISTRADOR/A";
    return "";
}